/* eslint-disable array-callback-return */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect } from "react";
import { useParams, NavLink } from "react-router-dom";
import { adminRoot } from "../../../constants/defaultValues";
import { popularApps, featuredApps } from "../../../constants/dummyApps";
import { data } from "../../../constants/featuredBox";
import appIcon from "../../../assets/appIconHover.svg";
import bgImage1 from "../../../assets/bgImage1.png";
import { Button, Dialog, DialogActions, createTheme, DialogContent, DialogContentText, DialogTitle, Grid, TextField } from "@mui/material";
import { ThemeProvider } from "@emotion/react";
import { green, purple } from '@mui/material/colors';
import ReportIcon from "@mui/icons-material/Report";
import CodeIcon from "@mui/icons-material/Code";
import AddCircleIcon from "@mui/icons-material/AddCircle";
import { addFeedback, getUser } from "../../../helpers/Utils";

const response = {
  firstName: "",
  lastName: "",
  email: "",
  appName: "",
  appDesc: "",
  githubUrl: ""
}

const AppCollection = () => {
  const { id } = useParams();
  const iden = parseInt(id);
  const [isOpen, setIsOpen] = useState(false);
  const [datas, setDatas] = useState({});
  const [searchTerm, setSearchTerm] = useState("");
  const result1 = featuredApps.filter((item) => parseInt(item.id) === iden);
  const result2 = data.filter((item) => parseInt(item.id) === iden);

  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [email, setEmail] = useState('');
  const [appName, setAppName] = useState('');
  const [appDesc, setAppDesc] = useState('');
  const [github, setGithub] = useState('');

  const [res, setRes] = useState(response);

  const [open, setOpen] = React.useState(false);
  const [ids, setIds] = useState([]);
  const [apps, setApps] = useState([]);
  const [loading, setLoading] = useState(false);

  const [op, setOp] = useState(false);

  const [user, setUser] = useState({});
  const [formOpen, setFormOpen] = useState(false);

  const handleFormOpen = () => {
    setFormOpen(false);
  };

  const handleClickOpen = () => {
    setOp(true);
  };

  const handleClose = () => {
    setOp(false);
    setDatas({});
  };

  useEffect(() => {
    getAllFunctionApps();
  }, [])
  useEffect(() => {
    if (ids) {
      ids.forEach((id) => {
        getAppData(id);
      });
    }
    setLoading(true);
    return () => {
      setApps([]);
    };
  }, [ids])

  useEffect(() => {
    getUser()
      .then(e => setUser(e));
  }, []);


  const getAllFunctionApps = async () => {
    const options = {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        id: result2[0].apps,
      }),
    }
    await fetch("/platform/api/app/searchFunctionId", options)
      .then(res => res.json())
      .then(data => setIds(data.fields.Apps))
    setLoading(true);
  }

  let count = 0;
  const getAppData = async (id) => {
    setApps([]);
    setLoading(true);
    const Data = {
      id: id,
    };
    await fetch("/platform/api/app/searchId", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(Data),
    })
      .then((res) => res.json())
      .then((datas) => {
        setApps((state) => [...state, datas]);
        count++;
        if (count === ids.length) {
          setLoading(false);
        }
      });
  };

  const sendMail = async () => {
    handleClose()
    const contactBody = {
      "firstName": user.firstName,
      "lastName": user.lastName,
      "email": user.email,
      "githubUrl": github,
      "appName": appName,
      "appDesc": appDesc
    }
    await fetch('/platform/api/app/addcontact',
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(contactBody)
      })
      .then(res => setIds(res))
      .catch(err => console.error(err))

    const mailBody = {
      "firstName": user.firstName,
      "toSend": user.email,
      "appName": appName,
      "githubUrl": github
    }
    await fetch(
      '/platform/api/app/sendMail',
      {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(mailBody)
      }
    )
  }

  const theme = createTheme({
    palette: {
      primary: {
        main: '#5c31ff',
      },
      secondary: {
        main: green[400],
      },
    },
  });

  const searchPopularApps = () => {
    const filteredApps = popularApps.filter((item) => {
      if (searchTerm === "") {
        return item;
      } else if (item.name.toLowerCase().includes(searchTerm.toLowerCase())) {
        return item;
      }
    });
    return filteredApps;
  };

  useEffect(() => {
    searchPopularApps();
  }, [searchTerm]);

  // const NoResult = () => {
  //   return (
  //     <div className="noResult noResultCollection">
  //       <img
  //         src="https://static.thenounproject.com/png/385145-200.png"
  //         alt=""
  //       />
  //       <h5>Sorry, we couldn't find any results!</h5>
  //     </div>
  //   );
  // };

  const FeedbackForm = () => {
    const [feedback, setFeedback] = useState('');

    const handleClickOpen = () => {
      setOpen(true);
    };

    const handleClose = () => {
      setOpen(false);
    };

    const handleSubmit = () => {
      addFeedback(user, feedback, datas.fields.Name);
      handleClose();
    }

    return (
      <div>
        <Dialog fullWidth={true} maxWidth='md' open={open} onClose={handleClose}>
          <DialogTitle>Feedback Form</DialogTitle>
          <DialogContent>
            <TextField
              autoFocus
              margin="dense"
              id="name"
              label="Enter your feedback"
              multiline={true}
              minRows={4}
              type="text"
              fullWidth
              variant="standard"
              name="appDesc"
              value={feedback}
              onChange={(e) => setFeedback(e.target.value)}
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose}>Cancel</Button>
            <Button onClick={handleSubmit}>Submit Feedback</Button>
          </DialogActions>
        </Dialog>
      </div>
    );
  }

  const AppDialog = () => {
    const theme = createTheme({
      components: {
        // Name of the component
        MuiDialogTitle: {
          styleOverrides: {
            // Name of the slot
            root: {
              // Some CSS
              padding: 0,
              borderBottom: "1px solid lightgray",
            },
          },
        },
        MuiDialogContent: {
          styleOverrides: {
            root: {
              padding: 0,
            },
          },
        },
      },
    });

    const [scroll, setScroll] = React.useState("body");
    setIsOpen(true);

    const handleClose = () => {
      setIsOpen(false);
      setDatas({});
    };

    const descriptionElementRef = React.useRef(null);
    React.useEffect(() => {
      if (isOpen) {
        const { current: descriptionElement } = descriptionElementRef;
        if (descriptionElement !== null) {
          descriptionElement.focus();
        }
      }
    }, [isOpen]);

    return (
      <ThemeProvider theme={theme}>
        <div>
          <Dialog
            sx={{
              height: "100vh",
              margin: "0 auto",
              overflow: "hidden",
            }}
            open={isOpen}
            maxWidth={"lg"}
            onClose={handleClose}
            scroll={scroll}
            aria-labelledby="scroll-dialog-title"
            aria-describedby="scroll-dialog-description"
            className="dialogBox"
          >
            <DialogTitle id="scroll-dialog-title">
              <div className="dialogTop">
                <img src={datas.fields.AppICON[0].url} alt="" />
                <div className="topDesc">
                  <h3>{datas.fields.Name}</h3>
                  <p className="shortDesc">{datas.fields.ShortDescription}</p>
                  <div className="categoryDesc">
                    <p>
                      <img
                        className="pricingIcon"
                        src={datas.fields.PricingICON[0].thumbnails.small.url}
                        alt="logo"
                      />
                      Built by {datas.fields.BuiltBy}
                    </p>
                    <p>
                      <img
                        className="pricingIcon"
                        src={datas.fields.PricingICON[0].thumbnails.small.url}
                        alt="logo"
                      />
                      {datas.fields.Pricing}
                    </p>
                  </div>
                </div>
                <span className="closeSpan" onClick={handleClose}>
                  x
                </span>
              </div>
            </DialogTitle>
            <DialogContent dividers={scroll === "body"}>
              <DialogContentText
                id="scroll-dialog-description"
                ref={descriptionElementRef}
                tabIndex={-1}
              >
                <div className="appDialogContent">
                  <div className="appContentTop">
                    <div className="contentLeft">
                      <h1>App Description</h1>
                      <p>
                        {datas.fields.LongDescription}
                      </p>
                      {/* <div className="appAudience">
                        <h4>
                          Audience :
                          <span className="audienceList">
                            Business Analyst, Marketing Professional, Customer
                            Access, App Developer
                          </span>
                        </h4>
                      </div> */}
                      <a
                        target="_blank"
                        rel="noreferrer"
                        href={datas.fields.LinkToLearnMore}
                      >
                        Learn more about {datas.fields.Name} App here
                      </a>
                    </div>
                    <div className="contentRight">
                      <h4>Try this yourself</h4>
                      <p style={{ fontSize: "15px" }}>
                      </p>
                      <ol>
                        <li>
                          Get access to the notebook: Modify the code, add lines, and make it yours.
                        </li>
                        <li>
                          Review the Prediction Results: Customise the results with your work data.
                        </li>
                        <li>
                          Interact with model with the Prebuilt App. Modify and showcase the app with others.
                        </li>
                      </ol>
                      <button className="feedbackBtn">
                        <ReportIcon /> <span onClick={() => setOpen(true)}>Send Feedback</span>
                      </button>
                      {datas.fields.NotebookExists && <button className="feedbackBtn">
                        <CodeIcon /> <a rel="noreferrer" target="_blank" href={datas.fields.NotebookLink}>Launch Notebook</a>
                      </button>}

                      {datas.fields.AppExists && <button className="feedbackBtn">
                        <AddCircleIcon /> <a rel="noreferrer" target="_blank" href={datas.fields.AppLink}>Launch App</a>
                      </button>}
                      {
                        datas.fields.Category > 0
                        &&
                        <>
                          <h4 style={{ marginBottom: "10px" }}>Categories</h4>
                          {/* <span className="categorySpan">{getCategory(datas.fields.id)}</span> */}
                        </>
                      }
                      {/* <video
                        style={{ marginTop: "20px" }}
                        width="320"
                        height="240"
                        controls
                      >
                        <source
                          src="https://www.youtube.com/watch?v=cGl5CqSiPLw"
                          type="video/webm"
                        />
                      </video> */}
                    </div>
                  </div>
                  {/* <div className="appContentBottom">
                    <h3 style={{ marginTop: "20px", marginLeft: "20px" }}>
                      People who viewed {datas.fields.Name} also viewed
                    </h3>
                    <div className="alsoViewed">
                      {popularApps.map((item) => {
                        return (
                          <div className="smallAppPopup">
                            <img
                              className="appLogo"
                              key={item}
                              src={item.fields.AppICON[0].url}
                              alt="asg"
                            />
                            <div className="appSmallInfo">
                              <h4>{item.fields.Name}</h4>
                              <p>{item.fields.ShortDescription.length >= 50 ? item.fields.ShortDescription.slice(0, 50) + "..." : item.fields.ShortDescription}</p>
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  </div> */}
                </div>
              </DialogContentText>
            </DialogContent>
          </Dialog>
        </div>
      </ThemeProvider>
    );
  };

  return (
    <>
      <>
        <div className="collectionPage">
          <div className="collectionPageTop">
            <div className="appHead collectionHead">
              <div className="appHeadLeft collectionHeadLeft">
                <h1 style={{ fontSize: "23px" }} className="appHeading">
                  {" "}
                  <i
                    className="simple-icon-grid"
                    style={{ color: '#5c31ff', marginRight: '5px' }}
                  />
                  {/* <img
                  style={{ height: "25px", width: "25px", marginRight: "5px" }}
                  src={appIcon}
                  alt="df"
                />{" "} */}
                  App Store
                </h1>
                <p className="appDesc">
                  Marketplace of Solution Accelerators including Pre Built Code,
                  Notebooks, and step by step instructions to get started.
                </p>
                <button
                  onClick={handleClickOpen}
                  style={{
                    fontSize: "15px",
                    color: "#5c31ff",
                    marginTop: "15px",
                    marginBottom: "-10px",
                    border: 'none'
                  }}
                >
                  <i className="iconsminds-open-book" /> Submit to App Catalogue
                </button>
              </div>
              <div className="appHeadRight collectionHeadRight">
                {/* <div className="inputBox">
                <i className="simple-icon-magnifier" />
                <input
                  placeholder="Search for apps..."
                  className="appSearch"
                  type="text"
                  onChange={(e) => setSearchTerm(e.target.value)}
                  value={searchTerm}
                />
              </div> */}
              </div>
            </div>
          </div>
          <div className="collectionPageBottom">
            <div className="leftInfo">
              <NavLink
                style={{
                  color: `${iden > 10 ? result1[0].color : result2[0].color}`,
                  fontSize: "18px",
                  fontWeight: '600'
                }}
                to={`${adminRoot}/apps`}
              >
                <i
                  style={{ fontSize: "14px", fontWeight: '700' }}
                  className="simple-icon-arrow-left"
                />{" "}
                Back to App Store
              </NavLink>
              <br />
              <h1
                style={{
                  color: `${iden > 10 ? result1[0].color : result2[0].color}`,
                }}
              >
                {iden > 10 ? result1[0].name : result2[0].shortDesc}
              </h1>
              <p
                style={{
                  color: `${iden > 10 ? result1[0].color : result2[0].color}`,
                }}
              >
                {iden > 10 ? result1[0].longDesc : result2[0].longDesc}
              </p>
            </div>
            <img
              src={iden > 10 ? bgImage1 : result2[0].bgImage}
              alt="imagebg"
              className="collectionImage"
            />
            {loading ? (<div className="loading" />) :
              (<Grid container className="collectionRight">
                {" "}
                {iden > 10
                  ? result1[0].apps.map((item) => {
                    return (
                      <Grid item xs={6}>
                        <button class="smallAppContainer col-4" onClick={() => {
                          setDatas(item);
                          setIsOpen(true);
                        }}>
                          <div className="smallApp smallAppCollection">
                            <div className="appSmallInfo">
                              <img
                                className="appLogo"
                                style={{ height: "60px" }}
                                src={item.fields.CategoryIcon[0].thumbnails.large.url}
                                alt="asg"
                              />
                              <h4>{item.fields.Name?.length >= 30 ? item.fields.Name.slice(0, 30) + "..." : item.fields.Name}</h4>
                              <p>{item.fields.ShortDescription?.length >= 30 ? item.fields.ShortDescription.slice(0, 30) + "..." : item.fields.ShortDescription}</p>

                            </div>
                            <div>
                              {item.fields.Pricing === 'Paid' && <p className="pricingTag">{item.fields.Pricing}</p>}
                            </div>
                          </div>
                        </button>
                      </Grid>
                    );
                  })
                  : apps.filter((app) => app.fields.BuiltBy === 'Katonic').map((item) => {
                    return (
                      <Grid item xs={4}>
                        <button class="smallAppContainer col-4" onClick={() => {
                          setDatas(item);
                          setIsOpen(true);
                        }}>
                          <div className="smallApp smallAppCollection">
                            <div className="appSmallInfo">
                              <img
                                className="appLogo"
                                key={item}
                                src={item.fields.CategoryIcon[0].thumbnails.large.url}
                                alt="asg"

                              />
                              <h4>{item.fields.Name?.length >= 30 ? item.fields.Name.slice(0, 30) + "..." : item.fields.Name}</h4>
                              <p>{item.fields.ShortDescription?.length >= 30 ? item.fields.ShortDescription.slice(0, 30) + "..." : item.fields.ShortDescription}</p>

                            </div>
                            <div>
                              {item.fields.Pricing === 'Paid' && <p className="pricingTag">{item.fields.Pricing}</p>}
                            </div>
                          </div>
                        </button>
                      </Grid>
                    );
                  })}
              </Grid>)}
          </div>
          {isOpen && <AppDialog />}
        </div>
        <ThemeProvider theme={theme}>
          <Dialog open={formOpen} onClose={handleFormOpen}>
            <DialogTitle>App Suggestion</DialogTitle>
            <DialogContent>
              <DialogContentText>
                Submit your item suggestions to the Katonic team and we will review it as soon as possible.
              </DialogContentText>

              <Grid container spacing={2}>
                <Grid item xs={6}>
                  <TextField
                    autoFocus
                    margin="dense"
                    id="name"
                    label="First Name"
                    type="text"
                    fullWidth
                    variant="standard"
                    name="firstName"
                    value={firstName}
                    onChange={(e) => setFirstName(e.target.value)}
                  />
                </Grid>
                <Grid item xs={6}>
                  <TextField

                    margin="dense"
                    id="name"
                    label="Last Name"
                    type="text"
                    fullWidth
                    variant="standard"
                    name="lastName"
                    value={lastName}
                    onChange={(e) => setLastName(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField

                    margin="dense"
                    id="name"
                    label="Email Address"
                    type="email"
                    fullWidth
                    variant="standard"
                    name="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField

                    margin="dense"
                    id="name"
                    label="App Name"
                    type="text"
                    fullWidth
                    variant="standard"
                    name="appName"
                    value={appName}
                    onChange={(e) => setAppName(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField

                    margin="dense"
                    id="name"
                    label="App Description"
                    multiline={true}
                    minRows={3}
                    type="text"
                    fullWidth
                    variant="standard"
                    name="appDesc"
                    value={appDesc}
                    onChange={(e) => setAppDesc(e.target.value)}
                  />
                </Grid>
                <Grid item xs={12}>
                  <TextField

                    margin="dense"
                    id="name"
                    label="Link to Github"
                    type="text"
                    fullWidth
                    variant="standard"
                    name="githubUrl"
                    value={github}
                    onChange={(e) => setGithub(e.target.value)}
                  />
                </Grid>
              </Grid>
            </DialogContent>
            <DialogActions>
              <Button onClick={handleClose}>Cancel</Button>
              <Button variant="contained" onClick={sendMail}>Submit</Button>
            </DialogActions>
          </Dialog>
        </ThemeProvider>
      </>
      {open && <FeedbackForm />}
    </>
  );
};

export default AppCollection;
